import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { zoomIn } from 'react-animations';
import Radium, {StyleRoot} from 'radium';
import axios from "axios";

const styles = {
    zoomIn: {
        animation: 'x 0.5s',
        animationName: Radium.keyframes(zoomIn, 'zoomIn')
    }
};

const user_storage = "";

const parse = JSON.parse;
const stringify = JSON.stringify;

const auth = {
    clear: function (key)
    {
        if (localStorage && localStorage.getItem(key))
            return localStorage.removeItem(key);
        if (sessionStorage && sessionStorage.getItem(key))
            return sessionStorage.removeItem(key);
    },

    clearAppStorage: function ()
    {
        if (localStorage)
            localStorage.clear();
        if (sessionStorage)
            sessionStorage.clear();
    },

    get: function (key)
    {
        if (localStorage && localStorage.getItem(key))
            return parse(localStorage.getItem(key))
        if (sessionStorage && sessionStorage.getItem(key))
            return parse(sessionStorage.getItem(key));
        return null;
    },

    getUserInfos: function (userInfo = user_storage)
    {
        return auth.get(userInfo);
    },

    set: function (value, key, isLocalStorage)
    {
        if (isLocalStorage && localStorage)
            return localStorage.setItem(key, stringify(value));
        if (sessionStorage)
            return sessionStorage.setItem(key, stringify(value));
        return null;
    },

    setUserInfo: function(value = '', isLocalStorage = false, userInfo = user_storage) {
        return auth.set(value, userInfo, isLocalStorage);
    }
};

class Auth extends Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            fade: false,
            data_login: {
                email: "",
                password: ""
            },
            data_register: {
                username: "",
                email: "",
                password: "",
                confirm_password: ""
            },
            validating_login: false,
            validating_register: false,
            session: sessionStorage.getItem('userInfo')
        };

        this.handleSwitch = this.handleSwitch.bind(this);
        this.handleBlur = this.handleBlur.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(e)
    {
        const redirect_auth = e.target.id;

        switch (redirect_auth)
        {
            case "register":
                const dataRegister = this.state.data_register;
                if (this.state.validating_register && dataRegister.password === dataRegister.confirm_password)
                {
                    const requestURL = 'http://localhost:8080/signup';

                    axios.post(requestURL, {email: dataRegister.email, password: dataRegister.password})
                        .then( res => {
                            console.log(res.data);
                            //auth.redirect('/');
                        })
                        .catch( err => {
                            console.log(err);
                            //auth.redirect('/');
                        });
                }
                else
                    this.props.notifications('Something is wrong, please check out');
                break;
            case "login":
                const dataLogin = this.state.data_login;

                if (this.state.validating_login)
                {
                    const requestURL = 'http://localhost:8080/login';

					axios.post(requestURL, {email: dataLogin.email, password: dataLogin.password})
                        .then( res => {
                        	const { data } = res;

							console.log('Connected');

							auth.set(data, 'userInfo', true);
                        })
                        .catch( error => {
                            //Action en cas d'erreur
							console.log('Unable to connect');
                        });
                }
                else
                    this.props.notifications('Something is wrong.');
                break;
            default:
                break;
        }

        e.preventDefault();
    }

    handleSwitch()
    {
        const loginForm = document.getElementById('login-form');
        const registerForm = document.getElementById('register-form');

        loginForm.className = "login-form";
        registerForm.className = "register-form";

        if (!this.state.fade)
        {
            loginForm.className += " animated fadeOut";
            registerForm.className += " animated fadeIn";
            this.setState({fade: !this.state.fade});
        }
        else
        {
            loginForm.className += " animated fadeIn";
            registerForm.className += " animated fadeOut";
            this.setState({fade: !this.state.fade});
        }
    }

    handleBlur(e)
    {
        const name = e.target.name[0];

        switch (name)
        {
            case "l":
                const email = document.getElementById('l_email');
                const password = document.getElementById('l_password');
                if (email.value.length > 0 && password.value.length > 0)
                {
                    this.setState({
                        validating_login: true,
                        data_login: {
                            email: email.value,
                            password: password.value
                        }
                    });
                    document.getElementById('submit-login').disabled = false;
                }
                else
                {
                    this.setState({
                        validating_login: false,
                        data_login: {
                            email: "",
                            password: ""
                        }
                    });
                    document.getElementById('submit-login').disabled = true;
                }
                break;
            case "r":
                const checkbox = document.getElementById('cgu').checked;
                const elements = document.querySelectorAll('#register-form input');
                let count = 0;

                const uppercase = !(/^[^A-Z]*$/).test(elements[2].value);
                const numeric = /\d/.test(elements[2].value);
                const length = elements[2].value.length > 6;

                (uppercase) ? ((count < 3) ? count++ : null) : ((count > 0) ? count-- : null);
                (numeric) ? ((count < 3) ? count++ : null) : ((count > 0) ? count-- : null);
                (length) ? ((count < 3) ? count++ : null) : ((count > 0) ? count-- : null);

                switch (count)
                {
                    case 0:
                        document.getElementById('security-level').style.backgroundColor = "#ff4d4d";
                        break;
                    case 1:
                        document.getElementById('security-level').style.backgroundColor = "#ffaf40";
                        break;
                    case 2:
                        document.getElementById('security-level').style.backgroundColor = "#ffaf40";
                        break;
                    case 3:
                        document.getElementById('security-level').style.backgroundColor = "#32ff7e";
                        break;
                    default:
                        break;
                }

                for (let i = 0; i < elements.length - 1; i++)
                {
                    if (elements[i].value.length > 0 && checkbox)
                    {
                        this.setState({
                            validating_register: true,
                            data_register: {
                                username: elements[0].value,
                                email: elements[1].value,
                                password: elements[2].value,
                                confirm_password: elements[3].value,
                            }
                        });
                        document.getElementById('submit-register').disabled = false;
                    }
                    else
                    {
                        this.setState({
                            validating_register: false,
                            data_register: {
                                username: "",
                                email: "",
                                password: "",
                                confirm_password: "",
                            }
                        });
                        document.getElementById('submit-register').disabled = true;
                        break;
                    }
                }
                break;
            default:
                break;
        }
    }

    render()
    {
        if (!this.props.session)
        {
            return (
                <StyleRoot id="unlogged-content" style={styles.zoomIn}>
                    <h2>Hypertube</h2>
                    <div className="login-form" id="login-form">
                        <form method="post" id="login" className="align-items-column" onSubmit={this.handleSubmit}>
                            <div className="field">
                                <input
                                    type="email"
                                    name="l_email"
                                    id="l_email"
                                    className="form-control"
                                    placeholder="Email address"
                                    onChange={this.handleBlur}
                                    autoComplete="off"
                                />
                                <span><i className="fas fa-envelope"></i></span>
                            </div>

                            <div className="field">
                                <input
                                    type="password"
                                    name="l_password"
                                    id="l_password"
                                    className="form-control"
                                    placeholder="Password"
                                    onChange={this.handleBlur}
                                    autoComplete="off"
                                />
                                <span><i className="fas fa-key"></i></span>
                            </div>

                            <div className="options-form">
                                <input
                                    type="checkbox"
                                    name="remember-me"
                                    id="remember-me"
                                />
                                <label htmlFor="remember-me"></label>
                                Remember me

                                <a onClick={this.handleSwitch} style={{marginTop: 0, right: 0, position: "absolute"}}>I've forgot my password</a>
                            </div>
                            <button type="submit" id="submit-login" className="btn btn-green" disabled="true">
                                <i className="fas fa-lock-open"></i>
                            </button>
                        </form>
                        <a href="http://localhost:8080/auth/facebook">
                            <button type="button" className="btn btn-blue">
                                <i className="fab fa-facebook-f"></i>
                            </button>
                        </a>
                        <a href="http://localhost:8080/auth/42">
                            <button type="button" className="btn btn-42">
                                <img src="https://signin.intra.42.fr/assets/42_logo-7dfc9110a5319a308863b96bda33cea995046d1731cebb735e41b16255106c12.svg" height={20} width={20} />
                            </button>
                        </a>
                        <a onClick={this.handleSwitch}>I do not have an account yet</a>
                    </div>
                    <div className="register-form" id="register-form">
                        <form action="#" id="register" className="align-items-column" onSubmit={this.handleSubmit}>

                            <div className="field">
                                <input
                                    type="text"
                                    name="r_username"
                                    id="r_username"
                                    className="form-control"
                                    placeholder="Username"
                                    onChange={this.handleBlur}
                                    autoComplete="off"
                                />
                                <span><i className="fas fa-user"></i></span>
                            </div>

                            <div className="field">
                                <input
                                    type="email"
                                    name="r_email"
                                    id="r_email"
                                    className="form-control"
                                    placeholder="Email address"
                                    onChange={this.handleBlur}
                                    autoComplete="off"
                                />
                                <span><i className="fas fa-envelope"></i></span>
                            </div>

                            <div className="field">
                                <input
                                    type="password"
                                    name="r_password"
                                    id="r_password"
                                    className="form-control"
                                    placeholder="Password"
                                    onChange={this.handleBlur}
                                    autoComplete="off"
                                />
                                <span><i className="fas fa-key"></i></span>
                                <span style={{left: "unset", right: 0, top: 23}}>
                                    <div id="security-level" className="security-level"></div>
                                </span>
                            </div>

                            <div className="field">
                                <input
                                    type="password"
                                    name="r_confirm_password"
                                    id="r_confirm_password"
                                    className="form-control"
                                    placeholder="Confirm password"
                                    onChange={this.handleBlur}
                                    autoComplete="off"
                                />
                                <span><i className="fa fa-redo-alt fa-spin"></i></span>
                            </div>

                            <div className="options-form">
                                <input
                                    type="checkbox"
                                    name="r_cgu"
                                    id="cgu"
                                    onClick={this.handleBlur}
                                />
                                <label htmlFor="cgu"></label>
                                I accept the terms of <a href="">CGU</a>
                            </div>

                            <button type="submit" id="submit-register" className="btn btn-green" disabled="true">
                                <i className="fas fa-user"></i>
                            </button>
                        </form>
                        <a onClick={this.handleSwitch}>I already have an account</a>
                    </div>
                </StyleRoot>
            );
        }
        else
        {
            // Show off index page (Films, series)
        }
    }
}

Auth.propTypes = {
    fade: PropTypes.bool,
    data_login: PropTypes.shape({
        username: PropTypes.string.isRequired,
        password: PropTypes.string.isRequired
    }),
    data_register: PropTypes.shape({
        username: PropTypes.string.isRequired,
        email: PropTypes.string.isRequired,
        password: PropTypes.string.isRequired,
        confirm_password: PropTypes.string.isRequired
    }),
    validating_login: PropTypes.bool,
    validating_register: PropTypes.bool
};

export default Auth;



