import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Auth from './Auth';

class App extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {auth: false};
    }

    notifications(message , styles)
    {
        console.log(message);
    }

    render()
    {
        return (
            <div className="main-content">
                <Auth session={this.state.auth} notifications={this.notifications} />
                <footer>

                </footer>
            </div>
        );
    }
}

App.propTypes = {
  auth: PropTypes.bool
};

export default App;



